<template>
  <div>
    <div class="items">
      <table class="mdl-data-table mdl-js-data-table">
        <thead>
          <tr>
            <th>Material</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in itemParams" :key="item[fk]">
            <th>{{item.label}}</th>
            <th>{{item.price}}</th>
            <th>{{item.quantity}}</th>
            <th>{{ item.price * item.quantity }}</th>
          </tr>
        </tbody>
      </table>
    </div>
     <button
        class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--right custom-button--primary"
        @click="toggleDialog(true)"
      >
      Add
      </button>
    <dialog-view @cancel="validItemSelected=false" ref="addDialog">
      <span slot="button-text"> Add </span>
      <div slot="dialog-body">
        <autocomplete
          :data="data"
          :action="action"
          :field="field"
          :multiSelect="false"
          :placeholder="placeholder"
          :emitObjects="true"
          :displayChips="false"
          @selected="itemParam=$event"
          v-if="!validItemSelected"></autocomplete>
          <line-item-form :item="itemParam" ref="addForm" v-else></line-item-form>
      </div>
      <button
        type="button"
        slot="dialog-actions"
        class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
        v-if="validItemSelected"
        @click="addLineItems"
        > Add </button>
    </dialog-view>
  </div>
</template>
<script>
import { Autocomplete } from 'SHARED/autocomplete'
import { DialogView } from 'SHARED/dialog'
import { LineItemForm } from '.'
export default {
  name: 'line-items',
  components: {
    Autocomplete,
    DialogView,
    LineItemForm
  },
  props: {
    data: {
      type: Array,
      default: []
    },
    action: {
      type: Function,
      required: true
    },
    field: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: 'Type and Select'
    },
    fk: {
      type: String,
      required: true
    },
    items: {
      type: Array
    }
  },
  computed: {
    itemParam: {
      get () {
        return this.item
      },
      set (item) {
        let param = {
          label: item[this.field],
          quantity: '',
          price: ''
        }
        param[this.fk] = item.id
        this.validItemSelected = true
        this.$set(this, 'item', param)
      }
    },
    itemParams () {
      return this.items || this.lineItems
    }
  },
  methods: {
    addLineItems () {
      let form = this.$refs.addForm
      form.valid().then(valid => {
        if (valid) {
          let item = this.itemParams.find(item => {
            return form.item[this.fk] === item[this.fk]
          })
          if (!item) {
            let product = this.getItemParams(form.item)
            this.itemParams.push(product)
            this.total += (product.price * product.quantity)
            this.$refs.addDialog.closeModal()
            this.proceed()
          } else {
            this.$toastr.e('Item already added.')
          }
        }
      })
    },
    getItemParams (item) {
      let param = {
        label: item[this.field] || item.label,
        quantity: item.quantity,
        price: item.price
      }
      param[this.fk] = item[this.fk]
      return param
    },
    toggleDialog (show) {
      if (show) {
        this.$refs.addDialog.showModal()
      } else {
        this.$refs.addDialog.close()
      }
    },
    proceed () {
      if (this.valid()) {
        this.$emit('selectedLineItems', this.itemParams)
        this.$emit('totalSelectedItemsAmount', this.total)
      }
    },
    valid () {
      if (this.itemParams.length > 0) {
        return true
      } else {
        this.$toastr.e('Please select items')
        return false
      }
    }
  },
  data () {
    return {
      lineItems: this.items || [],
      item: {
        label: '',
        price: '',
        quantity: ''
      },
      validItemSelected: false,
      total: 0
    }
  }

}
</script>



